.xelf {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.pannel {
    background: linear-gradient(360deg,#2196F3,#ffa50000);
    color: white;
    padding: 1rem;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    letter-spacing: 9px;
}

.block {
    background: #111;
    display: inline-block;
    padding: 0.2rem 1.4rem;
    margin: 1px;
    border-radius: 5px;
    position: relative;
    text-align: center;
}
.blocklabel {
    font-size: 11px;
}
.progress {
    display: inline;
}
.level {
        background: red;  
}
.next {
    background: orange;
    padding: 0.2rem 2rem;
    cursor: pointer;
}
.right {
    float: right;
}

.btn-txt{    position: relative;
    bottom: 5px;}
    .center{
        position: relative;
        top: -15px;
    }


    
@media screen and (max-width: 600px) {
    .pannel {
        letter-spacing: 2px;
    }
    .next{
        padding: 0.1rem 1rem;
    }
  }
.flip {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.inner {
    background: linear-gradient(to top, white 50%, black 53%, black 55%, red 50%);
    color: white;
    transition: 0.5s linear;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    box-shadow: 3px 2px 20px 0px #3f51b575, inset 2px 0px 20px 0px black;
    margin: 4px;
}

.inner,
.back,
.front {
    height: 100px;
    width: 100px;
    border-radius: 50%;
}

.back {
    transition: 0.5s;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.back,
.front {
    text-align: center;
    position: absolute;
    -webkit-backface-visibility: hidden!important;
            backface-visibility: hidden!important;
    top: 0;
}

.inner:after {
    content: '';
    position: absolute;
    height: 7px;
    width: 7px;
    background: white;
    border-radius: 50%;
    top: 44%;
    left: 49%;
    box-shadow: 0px 0px 0 5px black;
}

.hide {
    -webkit-animation: rotate 2s linear 1;
            animation: rotate 2s linear 1;
    opacity: 0;
    position: absolute;
    z-index: -9999;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
        position: relative;
        z-index: 1;
    }
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        opacity: 0.5;
        z-index: 1;
        position: relative;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
        position: relative;
        z-index: 1;
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
        position: relative;
        z-index: 1;
    }
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        opacity: 0.5;
        z-index: 1;
        position: relative;
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
        position: relative;
        z-index: 1;
    }
}

.front {
    opacity: 0.3;
}



