
.pannel {
    background: linear-gradient(360deg,#2196F3,#ffa50000);
    color: white;
    padding: 1rem;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    letter-spacing: 9px;
}

.block {
    background: #111;
    display: inline-block;
    padding: 0.2rem 1.4rem;
    margin: 1px;
    border-radius: 5px;
    position: relative;
    text-align: center;
}
.blocklabel {
    font-size: 11px;
}
.progress {
    display: inline;
}
.level {
        background: red;  
}
.next {
    background: orange;
    padding: 0.2rem 2rem;
    cursor: pointer;
}
.right {
    float: right;
}

.btn-txt{    position: relative;
    bottom: 5px;}
    .center{
        position: relative;
        top: -15px;
    }


    
@media screen and (max-width: 600px) {
    .pannel {
        letter-spacing: 2px;
    }
    .next{
        padding: 0.1rem 1rem;
    }
  }