.flip {
    transform: rotateY(180deg);
}

.inner {
    background: linear-gradient(to top, white 50%, black 53%, black 55%, red 50%);
    color: white;
    transition: 0.5s linear;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    box-shadow: 3px 2px 20px 0px #3f51b575, inset 2px 0px 20px 0px black;
    margin: 4px;
}

.inner,
.back,
.front {
    height: 100px;
    width: 100px;
    border-radius: 50%;
}

.back {
    transition: 0.5s;
    transform: rotateY(180deg);
}

.back,
.front {
    text-align: center;
    position: absolute;
    backface-visibility: hidden!important;
    top: 0;
}

.inner:after {
    content: '';
    position: absolute;
    height: 7px;
    width: 7px;
    background: white;
    border-radius: 50%;
    top: 44%;
    left: 49%;
    box-shadow: 0px 0px 0 5px black;
}

.hide {
    animation: rotate 2s linear 1;
    opacity: 0;
    position: absolute;
    z-index: -9999;
}

@keyframes rotate {
    0% {
        transform: scale(1);
        opacity: 1;
        position: relative;
        z-index: 1;
    }
    50% {
        transform: scale(1.5);
        opacity: 0.5;
        z-index: 1;
        position: relative;
    }
    100% {
        transform: scale(1);
        opacity: 0;
        position: relative;
        z-index: 1;
    }
}

.front {
    opacity: 0.3;
}